// ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÇ¨Ïù¥ÎìúÏóêÏÑú Í∞ÑÎã®Ìïú Ìï¥Ïã± (Ïã§Ï†ú Ïö¥ÏòÅÏóêÏÑúÎäî ÏÑúÎ≤ÑÏóêÏÑú Ï≤òÎ¶¨Ìï¥Ïïº Ìï®)
export class PasswordUtils {

  // Í∞ÑÎã®Ìïú Ìï¥Ïãú Ìï®Ïàò (Ïã§Ï†úÎ°úÎäî bcrypt Îì±ÏùÑ ÏÑúÎ≤ÑÏóêÏÑú ÏÇ¨Ïö©Ìï¥Ïïº Ìï®)
  static async hashPassword(password: string): Promise<string> {
    const encoder = new TextEncoder();
    const data = encoder.encode(password + 'salt_for_store_system');
    const hashBuffer = await crypto.subtle.digest('SHA-256', data);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    return hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  }

  // ÎπÑÎ∞ÄÎ≤àÌò∏ Í≤ÄÏ¶ù
  static async verifyPassword(password: string, hash: string): Promise<boolean> {
    const hashedInput = await this.hashPassword(password);
    return hashedInput === hash;
  }

  // ÏûÑÏãú ÎπÑÎ∞ÄÎ≤àÌò∏ ÏÉùÏÑ±
  static generateTempPassword(): string {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
    let result = '';
    for (let i = 0; i < 8; i++) {
      result += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return result;
  }

  // ÎπÑÎ∞ÄÎ≤àÌò∏ Í∞ïÎèÑ Í≤ÄÏÇ¨
  static validatePassword(password: string): { valid: boolean; errors: string[] } {
    const errors: string[] = [];

    if (password.length < 4) {
      errors.push('ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÏµúÏÜå 4ÏûêÎ¶¨ Ïù¥ÏÉÅÏù¥Ïñ¥Ïïº Ìï©ÎãàÎã§.');
    }

    if (password.length > 20) {
      errors.push('ÎπÑÎ∞ÄÎ≤àÌò∏Îäî 20ÏûêÎ¶¨Î•º Ï¥àÍ≥ºÌï† Ïàò ÏóÜÏäµÎãàÎã§.');
    }

    return {
      valid: errors.length === 0,
      errors
    };
  }
}