import React, { useState, useEffect, Suspense, lazy } from 'react';
import { User, UserWithSchedule } from '../types';
import { UserService } from '../services/userService';
import UserList from '../components/UserManagement/UserList'; // Í∏∞Î≥∏ ÌÉ≠Ïù¥ÎØÄÎ°ú Ï¶âÏãú Î°úÎìú
import ConfirmDialog from '../components/ConfirmDialog'; // ÏûëÏùÄ Ïª¥Ìè¨ÎÑåÌä∏Ïù¥ÎØÄÎ°ú Ï¶âÏãú Î°úÎìú
import '../styles/components/ModernEmployeeManagement.css';

// Ï°∞Í±¥Î∂Ä Î†åÎçîÎßÅÎêòÎäî ÌÅ∞ Ïª¥Ìè¨ÎÑåÌä∏Îì§ÏùÄ lazy loading Ï†ÅÏö©
const UserForm = lazy(() => import('../components/UserManagement/UserForm'));
const ScheduleManagement = lazy(() => import('../components/UserManagement/ScheduleManagement'));
const FixedScheduleManagement = lazy(() => import('../components/UserManagement/FixedScheduleManagement'));
const WorkTimeAnalysis = lazy(() => import('../components/UserManagement/WorkTimeAnalysis'));

type TabType = 'users' | 'schedule' | 'fixed-schedule' | 'analysis';

const UserManagementPage: React.FC = () => {
  const [activeTab, setActiveTab] = useState<TabType>('users');
  const [users, setUsers] = useState<User[]>([]);
  const [usersWithSchedule, setUsersWithSchedule] = useState<UserWithSchedule[]>([]);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [showUserForm, setShowUserForm] = useState(false);
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [userToDelete, setUserToDelete] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    loadUsers();
  }, []);

  const loadUsers = async () => {
    try {
      setLoading(true);
      setError(null);
      const [allUsers, usersWithSched] = await Promise.all([
        UserService.getAllUsers(),
        UserService.getUsersWithCurrentSchedule()
      ]);
      setUsers(allUsers);
      setUsersWithSchedule(usersWithSched);
    } catch (err) {
      setError('ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      console.error('Error loading users:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleCreateUser = () => {
    setSelectedUser(null);
    setShowUserForm(true);
  };

  const handleEditUser = (user: User) => {
    setSelectedUser(user);
    setShowUserForm(true);
  };

  const handleDeleteUser = (user: User) => {
    setUserToDelete(user);
    setShowConfirmDialog(true);
  };

  const confirmDeleteUser = async () => {
    if (!userToDelete) return;

    try {
      await UserService.deleteUser(userToDelete.id!);
      await loadUsers();
      setShowConfirmDialog(false);
      setUserToDelete(null);
    } catch (err) {
      setError('ÏÇ¨Ïö©Ïûê ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      console.error('Error deleting user:', err);
    }
  };

  const handleUserFormSubmit = async (userData: Omit<User, 'id' | 'created_at' | 'updated_at'>, generateLogin?: boolean) => {
    try {
      if (selectedUser) {
        // Í∏∞Ï°¥ ÏÇ¨Ïö©Ïûê ÏóÖÎç∞Ïù¥Ìä∏
        await UserService.updateUser(selectedUser.id!, userData);

        // Î°úÍ∑∏Ïù∏ Í∂åÌïú Î≥ÄÍ≤ΩÏù¥ ÏûàÏúºÎ©¥ Ï≤òÎ¶¨
        if (generateLogin !== undefined) {
          const hasCurrentAccess = !!selectedUser.password_hash;
          if (generateLogin !== hasCurrentAccess) {
            await UserService.toggleUserLoginAccess(selectedUser.id!, generateLogin);
          }
        }
      } else {
        // ÏÉà ÏÇ¨Ïö©Ïûê ÏÉùÏÑ±
        const newUser = await UserService.createUser(userData, generateLogin);

        // Î°úÍ∑∏Ïù∏ Í∂åÌïúÏù¥ Î∂ÄÏó¨ÎêòÏóàÍ≥† ÏûÑÏãú ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏûàÏúºÎ©¥ ÏïåÎ¶º
        if (generateLogin && newUser.password_temp) {
          alert(`ÏßÅÏõêÏù¥ Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.\n\nÏûÑÏãú Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥:\nÏù¥Î©îÏùº: ${newUser.email}\nÎπÑÎ∞ÄÎ≤àÌò∏: ${newUser.password_temp}\n\nÏù¥ Ï†ïÎ≥¥Î•º ÏßÅÏõêÏóêÍ≤å ÏïàÏ†ÑÌïòÍ≤å Ï†ÑÎã¨Ìï¥Ï£ºÏÑ∏Ïöî.`);
        }
      }

      await loadUsers();
      setShowUserForm(false);
      setSelectedUser(null);
    } catch (err) {
      setError('ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      console.error('Error saving user:', err);
    }
  };

  const handleResetPassword = async (user: User) => {
    const confirmReset = window.confirm(`"${user.full_name}" ÏßÅÏõêÏùò ÎπÑÎ∞ÄÎ≤àÌò∏Î•º Ïû¨ÏÑ§Ï†ïÌïòÏãúÍ≤†ÏäµÎãàÍπå?\nÏÉàÎ°úÏö¥ ÏûÑÏãú ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÏÉùÏÑ±Îê©ÎãàÎã§.`);

    if (confirmReset) {
      try {
        const newTempPassword = await UserService.resetPassword(user.id!);
        alert(`ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Ïû¨ÏÑ§Ï†ïÎêòÏóàÏäµÎãàÎã§.\n\nÏÉàÎ°úÏö¥ ÏûÑÏãú ÎπÑÎ∞ÄÎ≤àÌò∏: ${newTempPassword}\n\nÏù¥ Ï†ïÎ≥¥Î•º ÏßÅÏõêÏóêÍ≤å ÏïàÏ†ÑÌïòÍ≤å Ï†ÑÎã¨Ìï¥Ï£ºÏÑ∏Ïöî.`);
        await loadUsers();
      } catch (err) {
        setError('ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏÑ§Ï†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
        console.error('Error resetting password:', err);
      }
    }
  };

  // Ïª¥Ìè¨ÎÑåÌä∏ Î°úÎî©ÏùÑ ÏúÑÌïú fallback
  const ComponentLoader = () => (
    <div className="component-loading">
      <div className="loading-spinner"></div>
      <p>Ïª¥Ìè¨ÎÑåÌä∏Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...</p>
    </div>
  );

  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...</p>
      </div>
    );
  }

  return (
    <div className="user-management-modern">
      {/* Background Effects */}
      <div className="background-effects">
        <div className="gradient-orb orb-1"></div>
        <div className="gradient-orb orb-2"></div>
        <div className="gradient-orb orb-3"></div>
      </div>

      {/* Header Section */}
      <div className="modern-header">
        <div className="header-content">
          <div className="header-text">
            <h1 className="modern-title">
              <span className="title-icon">üë•</span>
              ÏßÅÏõê Í¥ÄÎ¶¨
            </h1>
            <p className="modern-subtitle">
              ÌåÄÏõêÎì§Ïùò Ï†ïÎ≥¥ÏôÄ Ïä§ÏºÄÏ§ÑÏùÑ Ïä§ÎßàÌä∏ÌïòÍ≤å Í¥ÄÎ¶¨ÌïòÏÑ∏Ïöî
            </p>
          </div>
          <div className="header-stats">
            <div className="stat-card">
              <div className="stat-number">{users.length}</div>
              <div className="stat-label">Ï¥ù ÏßÅÏõê</div>
            </div>
            <div className="stat-card">
              <div className="stat-number">{users.filter(u => u.is_active).length}</div>
              <div className="stat-label">ÌôúÏÑ± ÏßÅÏõê</div>
            </div>
          </div>
        </div>
      </div>

      {/* Error Toast */}
      {error && (
        <div className="error-toast">
          <div className="error-content">
            <span className="error-icon">‚ö†Ô∏è</span>
            <span className="error-text">{error}</span>
            <button onClick={() => setError(null)} className="error-close">
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
            </button>
          </div>
        </div>
      )}

      {/* Modern Tab Navigation */}
      <div className="modern-tabs">
        <div className="tab-container">
          {[
            { key: 'users', icon: 'üë§', label: 'ÏßÅÏõê Í¥ÄÎ¶¨', color: 'blue' },
            { key: 'schedule', icon: 'üìÖ', label: 'Ï£ºÍ∞Ñ Ïä§ÏºÄÏ§Ñ', color: 'purple' },
            { key: 'fixed-schedule', icon: 'üîí', label: 'Í≥†Ï†ï Ïä§ÏºÄÏ§Ñ', color: 'orange' },
            { key: 'analysis', icon: 'üìä', label: 'Í∑ºÎ¨¥ Î∂ÑÏÑù', color: 'green' }
          ].map(tab => (
            <button
              key={tab.key}
              className={`modern-tab ${activeTab === tab.key ? 'active' : ''} ${tab.color}`}
              onClick={() => setActiveTab(tab.key as TabType)}
            >
              <span className="tab-icon">{tab.icon}</span>
              <span className="tab-label">{tab.label}</span>
              {activeTab === tab.key && <div className="tab-indicator"></div>}
            </button>
          ))}
        </div>
      </div>

      {/* Content Area */}
      <div className="modern-content">
        <div className="content-wrapper">
          {activeTab === 'users' && (
            <UserList
              users={users}
              onCreateUser={handleCreateUser}
              onEditUser={handleEditUser}
              onDeleteUser={handleDeleteUser}
              onResetPassword={handleResetPassword}
            />
          )}

          {activeTab === 'schedule' && (
            <Suspense fallback={<ComponentLoader />}>
              <ScheduleManagement
                users={usersWithSchedule}
                onScheduleUpdate={loadUsers}
              />
            </Suspense>
          )}

          {activeTab === 'fixed-schedule' && (
            <Suspense fallback={<ComponentLoader />}>
              <FixedScheduleManagement
                users={users}
              />
            </Suspense>
          )}

          {activeTab === 'analysis' && (
            <Suspense fallback={<ComponentLoader />}>
              <WorkTimeAnalysis />
            </Suspense>
          )}
        </div>
      </div>

      {/* Modern User Form Modal */}
      {showUserForm && (
        <Suspense fallback={<ComponentLoader />}>
          <UserForm
            user={selectedUser}
            onSubmit={handleUserFormSubmit}
            onCancel={() => {
              setShowUserForm(false);
              setSelectedUser(null);
            }}
          />
        </Suspense>
      )}

      {/* Confirm Dialog */}
      <ConfirmDialog
        isOpen={showConfirmDialog}
        title="ÏßÅÏõê ÏÇ≠Ï†ú ÌôïÏù∏"
        message={`Ï†ïÎßêÎ°ú "${userToDelete?.full_name}" ÏßÅÏõêÏùÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå? Ïù¥ ÏûëÏóÖÏùÄ ÎêòÎèåÎ¶¥ Ïàò ÏóÜÏäµÎãàÎã§.`}
        onConfirm={confirmDeleteUser}
        onCancel={() => {
          setShowConfirmDialog(false);
          setUserToDelete(null);
        }}
        confirmText="ÏÇ≠Ï†ú"
        cancelText="Ï∑®ÏÜå"
        type="danger"
      />
    </div>
  );
};

export default UserManagementPage;